import React, { useEffect, lazy, Suspense } from 'react'
import {
    Routes,
    Route,
    useLocation
} from "react-router-dom";
import { useSelector } from 'react-redux';
import Loader from '../components/UI/Loader/Loader';

// pages
const ContactMePage = lazy(() => import('../pages/ContactMePage/ContactMePage'));
const FourZeroFour = lazy(() => import('../pages/errorsPages/404/404'));
const MainPage = lazy(() => import('../pages/MainPage/MainPage'));

// import LoginPage from '../pages/authPages/LoginPage/LoginPage';
// import RegisterPage from '../pages/authPages/RegisterPage/RegisterPage';
// import ActivateUserPage from '../pages/authPages/ActivateUserPage/ActivateUserPage';
// import AboutMePage from '../pages/AboutMePage/AboutMePage';
// import SettingsMainPage from '../pages/settingsPages/SettingsMainPage/SettingsMainPage';
// import EditNamePage from '../pages/settingsPages/EditNamePage/EditNamePage';
// import SessionsPage from '../pages/settingsPages/SessionsPage/SessionsPage';
// import EditUsernamePage from '../pages/settingsPages/EditUsernamePage/EditUsernamePage';
// import EditBioPage from '../pages/settingsPages/EditBioPage/EditBioPage';
// import EditPasswordPage from '../pages/settingsPages/EditPasswordPage/EditPasswordPage';
// import VisitorsIPsPage from '../pages/adminPages/VisitorsIPsPage/VisitorsIPsPage';
// import AdminMainPage from '../pages/adminPages/AdminMainPage/AdminMainPage';
// import VisitorIpPage from '../pages/adminPages/VisitorIpPage/VisitorIpPage';
// import ResetPasswordPage from '../pages/authPages/ResetPasswordPage/ResetPasswordPage';
// import EditPhotoPage from '../pages/settingsPages/EditPhotoPage/EditPhotoPage';
// import PersonalChatPage from '../pages/chatPages/PersonalChatPage/PersonalChatPage'


const LoginPage = lazy(() => import('../pages/authPages/LoginPage/LoginPage'));
const RegisterPage = lazy(() => import('../pages/authPages/RegisterPage/RegisterPage'));
const ActivateUserPage = lazy(() => import('../pages/authPages/ActivateUserPage/ActivateUserPage'));
const AboutMePage = lazy(() => import('../pages/AboutMePage/AboutMePage'));
const SettingsMainPage = lazy(() => import('../pages/settingsPages/SettingsMainPage/SettingsMainPage'));
const EditNamePage = lazy(() => import('../pages/settingsPages/EditNamePage/EditNamePage'));
const SessionsPage = lazy(() => import('../pages/settingsPages/SessionsPage/SessionsPage'));
const EditUsernamePage = lazy(() => import('../pages/settingsPages/EditUsernamePage/EditUsernamePage'));
const EditBioPage = lazy(() => import('../pages/settingsPages/EditBioPage/EditBioPage'));
const EditPasswordPage = lazy(() => import('../pages/settingsPages/EditPasswordPage/EditPasswordPage'));
const VisitorsIPsPage = lazy(() => import('../pages/adminPages/VisitorsIPsPage/VisitorsIPsPage'));
const AdminMainPage = lazy(() => import('../pages/adminPages/AdminMainPage/AdminMainPage'));
const VisitorIpPage = lazy(() => import('../pages/adminPages/VisitorIpPage/VisitorIpPage'));
const ResetPasswordPage = lazy(() => import('../pages/authPages/ResetPasswordPage/ResetPasswordPage'));
const EditPhotoPage = lazy(() => import('../pages/settingsPages/EditPhotoPage/EditPhotoPage'));
const PersonalChatPage = lazy(() => import('../pages/chatPages/PersonalChatPage/PersonalChatPage'));




function AppRouter() {
    const isUserAuthenticated = useSelector(state => state.auth.isAuthenticated)

    const isAdmin = useSelector(state => state.auth.user?.is_staff)

    const location = useLocation();

    useEffect(() => {
        window.scrollTo(0, 0);
    }, [location]);

    return (
        <Suspense fallback={
            <div className='loader_wrapper'>
                <Loader />
            </div>
        }>
            <Routes>
                <Route path="/">
                    <Route index={true} element={<MainPage />} />
                    <Route path="about-me" element={<AboutMePage />} />
                    <Route path="contact-me" element={<ContactMePage />} />

                    {!isUserAuthenticated ?
                        <Route path="users">
                            <Route index={true} element={<FourZeroFour />} />
                            <Route path="login" element={<LoginPage />} />
                            <Route path="activate-account" element={<ActivateUserPage />} />
                            <Route path="register" element={<RegisterPage />} />
                            <Route path="reset_password" element={<ResetPasswordPage />} />
                        </Route>
                        :
                        <>
                            <Route path="settings">
                                <Route index={true} path="" element={<SettingsMainPage />} />
                                <Route path="edit_name" element={<EditNamePage />} />
                                <Route path="edit_username" element={<EditUsernamePage />} />
                                <Route path="edit_bio" element={<EditBioPage />} />
                                <Route path="change_photo" element={<EditPhotoPage />} />
                                <Route path="sessions" element={<SessionsPage />} />
                                <Route path="change_password" element={<EditPasswordPage />} />
                            </Route>

                            {isAdmin &&
                                <Route path="admin">
                                    <Route index={true} path="" element={<AdminMainPage />} />
                                    <Route path="visitors_ips">
                                        <Route index={true} path="" element={<VisitorsIPsPage />} />
                                        <Route path=":id" element={<VisitorIpPage />} />
                                    </Route>
                                </Route>
                            }

                            <Route path="chat">
                                <Route index={true} path="" element={<PersonalChatPage />} />
                                <Route path="personal" element={<PersonalChatPage />} />
                            </Route>

                        </>
                    }

                    <Route path="*" element={<FourZeroFour />} />

                    {/* ! TODO delete */}
                    <Route path="chat">
                        <Route index={true} path="" element={<PersonalChatPage />} />
                        <Route path="personal" element={<PersonalChatPage />} />
                    </Route>
                    {/* ! TODO delete */}
                </Route>
            </Routes>
        </Suspense>
    )
}

export default AppRouter;